// @mui
import { Box, Card, Container, Typography } from '@mui/material';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import Markdown from '../../components/Markdown';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import { HOST_API, } from '../../config';
// sections
// hooks
import useAuth from '../../hooks/useAuth';
import ExamID from './examid';
// ----------------------------------------------------------------------
export default function DocsExam() {
  const { themeStretch } = useSettings();

  const {setting, user} = useAuth();

  return (
    <Page title="Docs">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Result Checker Subscription API"
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            { name: 'Home' },
          ]}
        />

       
          <Card>

            <Box sx={{ p: { xs: 3, md: 5 } }}>
              <Typography  sx={{ mb: 5 }}>
              This section contains the recommended flow for integrating  Result Checker Subscription services on the {setting?.general?.app_name} RESTful API.
                           </Typography>

        
                <Markdown children={`<hr> <h3>Available Endpoints</h3><br/> <p>To integrate the ${setting?.general?.app_name} Result Checker Subscription Payment RESTful API, the endpoints below applies:</p>
                <p><br></p><p>
                <ol> <li>exam</li> <li>quantity</li> </ol>
                
                `} />
           <ExamID />
           <Markdown children={`<hr> <h3>PURCHASE PRODUCT</h3><br/> <p>Using a POST method,  Result Checker bundle can with the endpoint below:</p>
                <p><br></p><p>
                <hr> <h3>Enpoint URL</h3><br/> <blockquote>${HOST_API}/api/exam</blockquote> <hr> <h3>Code Sample</h3><br/>
                <pre class="ql-syntax" spellcheck="false">
                $paypload = array(
                    'exam' => 1,
                     'quantity' => 1);
                     $ch = curl_init();
                     curl_setopt($ch, CURLOPT_URL, '${HOST_API}/api/exam');
                     curl_setopt($ch, CURLOPT_POST, 1);
                     curl_setopt($ch, CURLOPT_POSTFIELDS, json_encode($paypload));
                     curl_setopt($ch, CURLOPT_RETURNTRANSFER, true);
                     $headers = [
                          "Authorization: Token ${user?.apikey}",
                         'Content-Type: application/json'
                     ];
                     curl_setopt($ch, CURLOPT_HTTPHEADER, $headers);
                     $response = curl_exec($ch);
                     curl_close($ch);
                </pre><br>
                <hr> <h3>Expected Response</h3><br/>
                <pre class="ql-syntax" spellcheck="false">
                {
                    "username": "adex",
                    "amount": 100,
                    "quantity": 1,
                    "message": "WAEC Exam Pin Generated",
                    "oldbal": "91807",
                    "newbal": 91807,
                    "date": "2022-09-08 03:58:27",
                    "status": "success",
                    "request-id": "RESULTCHECKER_63195a53157b3",
                    "pin": " pin1<=>seral1"
                }
                </pre>
                
                `} />
            </Box>
          </Card>
      </Container>
    </Page>
  );
}
